html,
body {
  height: 100%; }

body {
  overflow-x: hidden;
  margin: 0 auto;
  background: #000;
  line-height: 1.5;
  font-family: 'Lato', Helvetica, Arial, sans-serif;
  letter-spacing: 0.2rem;
  font-size: 1.6rem; }

section {
  width: 100%; }

a {
  transition: all .2s ease-out;
  outline: none;
  text-decoration: none;
  opacity: 1;
  color: #000; }
  a:hover {
    opacity: 0.5; }
    a:hover img {
      transition: opacity .2s ease-in;
      opacity: 0.8; }
  a img {
    transition: opacity .2s ease-in; }

hr {
  border: 0;
  height: 1px; }

ul {
  list-style: none;
  padding-left: 0; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol {
  margin: 0; }

img {
  vertical-align: middle;
  max-width: 100%; }

input,
label,
select,
button,
textarea {
  display: inline-block;
  vertical-align: middle; }

input:focus {
  outline: 0; }

input,
textarea {
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box; }

button,
input[type="reset"],
input[type="button"],
input[type="submit"],
input[type="checkbox"],
input[type="radio"],
input[type="text"],
input[type="password"],
select {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  box-shadow: none;
  background: transparent;
  border: 1px solid; }

input[type="search"] {
  -webkit-appearance: textfield;
  -webkit-box-sizing: content-box; }

::-webkit-search-decoration {
  display: none; }

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 0.01px;
  text-overflow: "";
  border-radius: 0;
  outline: none; }

button {
  outline: none; }

.video-wrapper {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  margin: 0; }
  .video-wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

.is-visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0; }

.link-style {
  border-bottom: 1px solid currentColor;
  cursor: pointer; }

.logo-animation {
  pointer-events: none; }

.no-animation .logo-animation {
  display: none; }

.is-not-ms .logo-animation {
  overflow: hidden;
  position: absolute;
  width: 250px;
  height: 250px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.is-not-ms svg {
  width: 100%;
  height: 100%; }

.is-not-ms .path {
  stroke: #fbfbfb;
  fill: #fbfbfb;
  fill-opacity: 0; }

.is-not-ms .border {
  stroke-dasharray: 4900;
  stroke-dashoffset: 4900;
  animation: anim1 1.5s linear 1s forwards, anim4 1s linear 3s forwards; }

@keyframes anim1 {
  0% {
    stroke-dashoffset: 4900; }
  100% {
    stroke-dashoffset: 0; } }

.is-not-ms .letter1 {
  stroke-dasharray: 680;
  stroke-dashoffset: 680;
  animation: anim2 0.7s linear 2s forwards, anim4 1s linear 3s forwards; }

@keyframes anim2 {
  0% {
    stroke-dashoffset: 680; }
  100% {
    stroke-dashoffset: 0; } }

.is-not-ms .letter2 {
  stroke-dasharray: 910;
  stroke-dashoffset: 910;
  animation: anim3 0.7s linear 2s forwards, anim4 1s linear 3s forwards; }

@keyframes anim3 {
  0% {
    stroke-dashoffset: 910; }
  100% {
    stroke-dashoffset: 0; } }

@keyframes anim4 {
  0% {
    fill-opacity: 0; }
  100% {
    fill-opacity: 1; } }

.is-not-ms .shine {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  animation: anim5 0.4s linear 3.8s forwards; }

@keyframes anim5 {
  0% {
    background: rgba(255, 255, 255, 0.4);
    width: 0; }
  100% {
    background: rgba(255, 255, 255, 0);
    width: 200%; } }

.is-not-ms .main-container {
  animation: displayIntro 1.8s ease-out 4.2s forwards; }

@keyframes displayIntro {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.main-container {
  opacity: 0;
  z-index: 1;
  background: #fdfdfd;
  position: relative;
  padding: 20px; }

.is-ms .main-container {
  opacity: 1; }

.main__section {
  margin: 20px 0; }

.intro-wrapper {
  width: 100%; }

.intro {
  position: relative;
  margin: 50px auto 0;
  text-align: center; }

.intro__title {
  font-family: 'Merriweather', serif;
  font-weight: 700;
  font-size: 3.6rem;
  letter-spacing: 0.1rem;
  margin-bottom: 10px; }

.intro__desc,
.intro__links {
  font-weight: 400;
  font-size: 2rem; }

.intro__desc {
  margin-bottom: 30px;
  font-size: 2rem; }

.intro__links {
  font-size: 1.6rem; }
  .intro__links a {
    color: #212121; }

.intro__link {
  margin-bottom: 5px; }

.sub-title {
  color: #757575;
  font-size: 2rem;
  font-weight: 400;
  position: relative;
  margin-bottom: 30px;
  text-align: center; }
  .sub-title::after {
    content: "";
    height: 1px;
    width: 40px;
    background-color: #757575;
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%); }

.pj-wrapper {
  max-width: 740px;
  margin-right: auto;
  margin-left: auto;
  text-align: center; }
  .pj-wrapper a {
    color: #455a64; }

.pj-item {
  margin-bottom: 40px;
  position: relative; }
  .pj-item::after {
    content: "";
    height: 2px;
    width: 40px;
    background-color: #757575;
    position: absolute;
    opacity: 0.2;
    bottom: -20px;
    left: 50%;
    transform: translateX(-50%); }

.pj-item__title {
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 10px;
  letter-spacing: 0.15rem; }

.pj-item__desc {
  margin-bottom: 5px;
  font-size: 1.4rem;
  color: #757575; }

.footer {
  padding: 20px 0;
  font-size: 1.2rem;
  color: #757575;
  text-align: center; }
